/**
 * Song
 * JavaScript constructor 
 * 
 * 
 * 1.
 * 2.
 */

import {getLyric} from 'api/song'
import {ERR_OK} from 'api/config'
import {Base64} from 'js-base64'

export default class Song {
   constructor({id, mid, singer, name, album, duration, image, url}){
       this.id = id
       this.mid = mid
       this.singer = singer
       this.name = name
       this.album = album
       this.duration = duration
       this.image = image
       this.url = url
   }

   getLyric() {
       if(this.lyric){
           return Promise.resolve()
       }

       return new Promise((resolve, reject) => {
           getLyric(this.mid).then((res) => {
               if(res.retcode === ERR_OK){
                   this.lyric = Base64.decode(res.lyric)// 
                   console.log(this.lyric)
                   resolve(this.lyric)
               }else{
                   reject('no lyric')
               }
           })
       })
   }
}

//musicDataSong
export function createSong(musicData, songVkey){
    return new Song({
        id: musicData.songid,
        mid: musicData.songmid,
        singer: filterSinger(musicData.singer),
        name: musicData.songname,
        album: musicData.albumname,
        duration: musicData.interval, //s
        image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
        // url: `http://ws.stream.qqmusic.qq.com/${musicData.songid}.m4a?fromtag=46`  
        //guid
        url: `http://ws.stream.qqmusic.qq.com/C400${musicData.songmid}.m4a?vkey=${songVkey}&guid=4866459368&uin=0&fromtag=66`
   })
}

//singer
export function filterSinger(singer){
    let ret = []
    if(!singer){
        return ''
    }
    singer.forEach((s) => {
        ret.push(s.name)
    })
    return ret.join('/')
}

export function creatSongList (musicData, songVkey) {
    return new Song({
      id: musicData.id,
      mid: musicData.mid,
      singer: filterSinger(musicData.singer),
      name: musicData.name,
      album: musicData.albumname,
      duration: musicData.interval,
      image: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.album.mid}.jpg?max_age=2592000`,
      //guid
      url: `http://ws.stream.qqmusic.qq.com/C400${musicData.mid}.m4a?vkey=${songVkey}&guid=4866459368&uin=0&fromtag=66`
    })
  }
